.page,
.body {
  height: 100%;
}

.body {
  font-family: 'Open Sans', sans-serif;
  font-size: 16px;
  line-height: 27px;
  font-weight: 400;
  color: $default;
  overflow-x: hidden;
  min-width: 300px;
}

a {
  color: inherit;
}

.wrapper {
  display: flex;
  flex-direction: column;
  min-height: 100%;
}

.main {
  flex: 1 0 auto;
}

.footer {
  flex: 0 0 auto;
}

.container {
  max-width: 1140px;
  margin: 0 auto;
  padding: 0 15px;
}

.container-fluid {
  max-width: 1920px;
  margin: 0 auto;
  padding: 0 10px;
}

.visually-hidden {
  position: absolute;
  width: 1px;
  height: 1px;
  margin: -1px;
  border: 0;
  padding: 0;
  white-space: nowrap;
  clip-path: inset(100%);
  clip: rect(0 0 0 0);
  overflow: hidden;
}

// S l i c k  -  d o t s
.slick-dots {
  display: flex;
  justify-content: center;
  align-items: center;

  li {
    margin: 0 15px;
  }

  button {
    background-color: $title;
    border: none;
    width: 8px;
    height: 8px;
    border-radius: 50%;
    padding: 0;
    font-size: 0;
  }

  li.slick-active {
    button {
      background-color: #ffef35;
    }
  }
}

// I n f o  -  E m a i l
.info-email {
  display: inline-block;
  font-weight: 600;
  font-size: 14px;
  line-height: 28px;
  color: $title;
  margin-bottom: 23px;
  padding-left: 25px;
  position: relative;

  &:hover {
    opacity: 0.7;
  }

  &--icon {
    position: absolute;
    left: 1px;
    top: 7px;
  }
}

// S o c i a l
.social {
  display: flex;
  flex-wrap: wrap;
  margin-bottom: 20px;

  &__item+.social__item {
    margin-left: 40px;
  }

  &__link {
    padding: 5px 0;

    &:hover {
      opacity: 0.7;
    }
  }
}

// P o p u p  -  b l o c k
.popup-block {
  background-image: url('../img/content/about__popup.webp');
  background-repeat: no-repeat;
  background-position: top center;
  background-size: cover;
  min-height: 414px;
  display: flex;
  justify-content: center;
  align-items: center;

  &__play {
    &:hover {
      opacity: 0.7;
    }
  }
}

// T a g s
.tags {

  &__title {
    margin-bottom: 24px;
    color: $title;
    @extend %title-font;
    line-height: 23px;
  }

  &__link {
    display: inline-block;
    font-size: 14px;
    font-weight: 600;
    color: #8F8D8D;
    padding: 0 16px;
    margin-right: 10px;
    margin-bottom: 10px;
    border: 1px solid rgba(#454647, .5);
    border-radius: 14px;

    &:hover,
    &:active,
    &:focus {
      background-color: #ffef35;
      color: #363838;
      border: 1px solid #ffef35;
      transition: all .3s;
    }
  }
}

// F o r m 

.form {
  display: flex;
  justify-content: space-between;
  flex-wrap: wrap;

  &__input {
    width: 48%;
    border: 1px solid #dfdfdf;
    border-radius: 20px;
    padding: 6px 17px;
    margin-bottom: 13px;

    &::placeholder {
      color: #a1a1a1;
      font-size: 16px;
      font-family: 'Open Sans', sans-serif;
      line-height: 27px;
      font-weight: 400;
    }
  }

  &__area {
    width: 100%;
    margin-bottom: 20px;
    border: 1px solid #dfdfdf;
    border-radius: 24px;
    padding: 14px 17px;
    min-height: 125px;
    resize: none;

    &::placeholder {
      color: #a1a1a1;
      font-size: 16px;
      font-family: 'Open Sans', sans-serif;
      line-height: 27px;
      font-weight: 400;
    }
  }

  &__container {
    display: flex;
    flex-direction: column;
  }

  &__checkbox-label {
    position: relative;
    cursor: pointer;
    user-select: none;
    margin: 20px 0;
    margin-left: 30px;
    color: #a1a1a1;
  }

  &__checkbox-input {
    position: absolute;
    width: 1px;
    height: 1px;
    overflow: hidden;
    clip: rect(0 0 0 0);
    padding-left: 20px;
  }

  &__checkbox-span {
    position: absolute;
    width: 16px;
    height: 16px;
    border: 2px solid #a1a1a1;
    margin-left: -20px;
    top: 4px;
  }

  &__checkbox-input:checked+&__checkbox-span::before {
    content: "";
    width: 8px;
    height: 8px;
    background-color: #a1a1a1;
    position: absolute;
    top: 50%;
    left: 50%;
    transform: translate(-50%, -50%);
  }

  &__checkbox-input:disabled+&__checkbox-span {
    border: 2px solid gray;
  }

  &__checkbox-input:disabled:checked+&__checkbox-span::before {
    background-color: gray;
  }

  &__checkbox-input:focus+&__checkbox-span {
    box-shadow: 0 0 0 2px black;
  }

  &__btn {
    border: none;
    color: #fffff0;
    background-color: $title;
    font-weight: 600;
    border-radius: 21px;
    padding: 6px 12px;
    min-width: 180px;
    cursor: pointer;

    &:hover,
    &:focus,
    &:active {
      background-color: #ffef35;
      color: #363838;
    }
  }
}