@media (max-width: 1600px) {
  .top {
    &__inner::after {
      height: 460px;
      // width: 100%;
    }

    &__slider-title {
      font-size: 52px;
      line-height: 64px;
      width: 50%;
    }

    &__slider-img {
      // object-fit: contain;
      // width: 300px;
      height: 500px;
      width: 50%;
    }
  }
}

@media (max-width: 1260px) {
  .collection {
    grid-template-columns: repeat(auto-fill, minmax(200px, 1fr));
    gap: 30px;

    &__images {
      min-height: 300px;
    }
  }

  .top {
    &__inner::after {
      height: 350px;
      width: 100%;
    }

    &__slider-title {
      font-size: 42px;
      line-height: 54px;
      width: 40%;
    }

    &__slider-img {
      // object-fit: contain;
      // width: 300px;
      height: 400px;
      width: 40%;

    }
  }
}

@media (max-width: 992px) {
  .top {
    &__inner::after {
      height: 350px;
      width: 100%;
    }

    &__slider-info {
      width: 40%;
      margin-right: auto;
    }

    &__slider-title {
      font-size: 42px;
      line-height: 54px;
      // width: 40%;
    }

    &__slider-img {
      // object-fit: contain;
      // width: 300px;
      height: 400px;
      width: 40%;

    }
  }
  .gallery {
    &__buttons {
      margin-bottom: 20px;
    }
    &__btn {
      margin-bottom: 10px;
    }
  }

  .footer {
    &__info {
      width: 380px;
    }
  }
}

@media(max-width: 768px) {

  .header__btn,
  .rightside-menu {
    display: none;
  }

  .menu {
    &__list-item {
      padding-left: 0;

      &+.menu__list-item {
        padding-right: 0;
      }
    }
  }


  .logo {
    margin: 0;
  }


  .top {
    &__inner::after {
      height: 550px;
      width: 100%;
    }

    &__slider-item {
      display: flex;
      flex-direction: column;
    }

    &__slider-info {
      width: 40%;
      margin: 0 auto;
      padding-bottom: 20px;
    }

    &__slider-title {
      font-size: 32px;
      line-height: 45px;
      // width: 40%;
    }

    &__slider-img {



      height: 300px;
      width: 50%;

    }
  }

  .decor {
    padding: 130px 0 115px;

    &__title {
      font-size: 48px;
      line-height: 56px;
    }
  }

  .footer {
    &__inner {
      flex-wrap: wrap;
      justify-content: center;
    }

    &__info {
      width: 100%;
      margin-bottom: 50px;
    }

    &__info-text {
      max-width: 100%;
    }

    &__menu {
      margin-left: auto;
      margin-right: auto;
    }

    &__social {
      margin-left: auto;
      margin-right: auto;

    }
  }
  .works-path {
    &__title {
      font-size: 28px;
      line-height: 36px;
    }
    &__items-box {
      border-bottom: none;
      align-items: center;
      justify-content: center;
      flex-direction: column;
    }
    &__item {
      width: 100%;
    }

    &__item--consultation {
      margin-left: 0;
      flex-direction: column;
    }

    &__item--production {
      margin-left: 0;
      flex-direction: column;
      // align-items: flex-start;
    }

    &__item--measuring {
      padding-top: 0;
      margin-left: 0;
      flex-direction: column;
    }

    &__items-box::before {
      display: none;
    }
    
    &__item--production::after {
      display: none;
    }
    &__item--measuring::before {
      display: none;
    }
  }

  .gallery {
    &__buttons {
      margin-bottom: 20px;
    }
    &__btn {
      font-size: 12px;
      padding: 5px 12px;
      margin: 0 6px 10px 6px;
    }
    &__item:nth-child(1) {
      grid-column: 1/-1;
    }
    &__item {
      grid-column: span 2;
    }
  }

  .blog-box {
    margin-bottom: 60px;
    
    &__title {
      font-size: 28px;
      line-height: 36px;
    }
    &__items {
      grid-template-columns: 1fr;
      gap: 50px;
    }
    &__img-link {
      margin-bottom: 5px;
    }
    &__links {
      font-weight: 400;
      font-size: 12px;
      line-height: 1.1rem;
      margin-bottom: 10px;
    }
    &__item-title {
      font-size: 20px;
      line-height: 28px;
    }
  }
}

@media(max-width: 560px) {
  .logo {
    order: -1;
    position: relative;
    z-index: 12;

    img {
      height: 35px;
    }
  }

  .header__btn-menu {
    display: block;
    background-color: transparent;
    border: none;
    cursor: pointer;
    order: -1;
    z-index: 11;

    span {
      width: 37px;
      height: 3px;
      background-color: #000;
      border-radius: 4px;
      display: block;
      margin-bottom: 6px;
    }
  }

  .header__inner {
    flex-wrap: wrap;
    padding-top: 40px;
  }

  .menu {
    padding-right: 0;
    width: 100%;
    position: relative;
    z-index: 11;
    text-align: center;
    transform: translateY(-350px);
    transition: all .5s;

    &--open {
      transform: translateY(30px);
    }

    &__list {
      display: block;
      background-color: #fff;
    }

    &__list-item {
      padding-right: 0;
    }

    &__list-link {
      display: block;
      padding: 15px 0;
    }
  }

  .top {
    &__inner::after {
      // height: 60vh;
      height: 630px;
      width: 100%;
    }

    &__slider-item {
      display: flex;
      flex-direction: column;
    }

    &__slider-info {
      width: 50%;
      margin: 0 auto;
      padding-bottom: 20px;
    }

    &__slider-title {
      font-size: 28px;
      line-height: 36px;
      // width: 40%;
    }

    &__slider-img {
      // object-fit: contain;
      // width: 300px;
      height: 300px;
      width: 50%;
      // margin-left: 100px;
    }
  }

  .new-collection__title {
    font-size: 28px;
    line-height: 36px;
  }

  .decor {
    padding: 80px 0 70px;

    &__title {
      font-size: 32px;
      line-height: 42px;
    }
  }


}

@media(max-width: 375px) {
  .footer {
    &__inner {
      justify-content: space-between;
    }

    &__social {
      margin-left: 0;
    }

    &__form {
      display: block;
    }

    &__form-button {
      width: 100%;
      margin-left: 0;
      margin-top: 10px;
    }
  }
}