.breadcrumbs {
  background: rgba(#dfdfdf, .5);
  padding: 13px 14px;
  min-width: 290px;
  &__list {
    display: flex;
    overflow-x: auto;
    // flex-wrap: nowrap;
  }
  &__item {
    position: relative;
    padding-right: 10px;

    &::after {
      content: '/';
      // margin-left: 2px;
      // margin-right: 2px;
      position: absolute;
    }
  }

  &__link {
    white-space: nowrap;
  }

  &__item:last-of-type {
    opacity: 0.7;

    &::after {
      display: none;
    }
  }
}