.about {
  &__prev {
    background-image: url('../img/content/about.webp');
    background-repeat: no-repeat;
    background-position: top center;
    background-size: cover;
    padding: 160px 0px;
    text-align: center;
    margin-top: -53px;
  }
  &__prev-title {
    color: $title;
    @extend %title-font;
    font-size: 60px;
    line-height: 80px;
  }
  &__content {
    padding: 130px 0 140px;
  }
  &__title {
    color: $title;
    @extend %title-font;
    font-size: 36px;
    line-height: 60px;
    margin-bottom: 22px;
    padding-bottom: 22px;
    position: relative;
    &::after {
      position: absolute;
      content: '';
      bottom: 0;
      left: 0;
      width: 200px;
      height: 1px;
      background-color: #6D7278;
    }
  }
  &__text {
    padding-left: 284px;
    p{
      margin-bottom: 30px;
    }
  }
}

