.aside {
  width: 350px;
  margin-left: 30px;

  &__search {
    position: relative;
    margin-bottom: 40px;
  }

  &__search-input {
    padding: 6px 45px 6px 18px;
    border: 1px solid rgba($title, 0.7);
    border-radius: 20px;
    display: block;
    width: 100%;

    &::placeholder {
      font-family: 'Open Sans', sans-serif;
      font-weight: 400;
      font-size: 14px;
      line-height: 27px;
      color: #a1a1a1;
    }
  }

  &__search-btn {
    border: none;
    background-color: transparent;
    position: absolute;
    top: 10px;
    right: 15px;
    cursor: pointer;
  }

  &__category {
    margin-bottom: 36px;
  }

  &__category-title {
    margin-bottom: 15px;
    color: $title;
    @extend %title-font;
    line-height: 23px;
  }

  &__category-item {
    margin-bottom: 12px;
  }

  &__category-link {
    color: inherit;

    &:hover {
      text-decoration: underline;
    }
  }

  &__posts {
    margin-bottom: 40px;

    &-title {
      margin-bottom: 24px;
      color: $title;
      @extend %title-font;
      line-height: 23px;
    }

    &-item {
      margin-bottom: 11px;
    }

    &-item-title {
      display: block;
      color: inherit;

      &:hover {
        text-decoration: underline;
      }
    }

    &-date,
    &-author {
      color: #a1a1a1;
      font-size: 14px;
    }

    &-author:hover {
      text-decoration: underline;
    }
  }

  &__tags {
    margin-bottom: 60px;
  }

}

.blog {
  margin: 60px 0 100px;

  &__inner {
    display: flex;
    justify-content: space-between;
  }

  &__items {
    max-width: 730px;
  }

  &__item {
    margin-bottom: 80px;
  }

  &__item-link {
    &:hover {
      text-decoration: underline;
    }
  }

  &__item-img {
    max-width: 730px;
    width: 100%;
  }

  &__item-info {
    padding: 25px 0 10px;
    font-weight: 600;
    font-size: 14px;
    color: #707070;
  }

  &__item-author:hover,
  &__item-author:focus,
  &__item-author:active,
  &__item-theme:hover,
  &__item-theme:focus,
  &__item-theme:active {
    text-decoration: underline;
  }

  &__item-title {
    font-size: 24px;
    line-height: 34px;
    margin-bottom: 6px;
    color: $title;
    @extend %title-font;
  }

  &__blockquote {
    background-color: rgba(223, 223, 223, 0.5);
    padding: 54px 64px;
    font-style: italic;
    font-weight: 300;
    font-size: 24px;
    line-height: 38px;
    text-align: center;
    margin-bottom: 80px;
  }

  &__blockquote-author {
    padding-top: 23px;
    margin-top: 23px;
    position: relative;
    font-weight: 700;
    font-size: 16px;
    line-height: 22px;
    @extend %title-font;
    font-style: normal;

    &::before {
      @extend %line;
      top: 0;
      bottom: none;
      left: 0;
      right: 0;
      margin: 0 auto;
    }
  }
}

.social__item--blog+.social__item--blog {
  margin-left: 20px;
}

.popup-block.popup-block--blog {
  background-image: url('../img/content/blog-video-popup-img.webp');
}

.blog__item-slider {
  margin-right: 10px;
  margin-bottom: 0;
}

.article-slider__img {
  max-width: 100%;
  max-height: 486px;
  width: 100%;
  height: 486px;
  object-fit: cover;
}


//Pagination
.pagination {
  width: 30%;
  margin: 0 auto;
  margin-top: 80px;
}

.pagination__list {
  display: flex;
  flex-wrap: wrap;
  gap: 15px;

  &-num {
    border: 1px solid black;
    background-color: transparent;
    padding: 5px 10px;
    color: black;
    opacity: 0.6;

    &:hover {
      opacity: 1;
    }
  }

  &-num--active {
    background-color: #ffef35;
    border: 1px solid black;
    padding: 5px 10px;
    color: black;
    opacity: 0.6;
    cursor: default;
  }
}

.pagination__arrow {
  position: relative;
  padding: 10px 5px 5px 5px;
  display: inline-block;
  opacity: 0.6;

  &:hover {
    opacity: 1;
  }

  &::after {
    content: '';
    position: absolute;
    width: 2px;
    height: 10px;
    border-radius: 3px;
    background-color: #000000;
    // opacity: 0.7;
    top: 0;
    left: 0px;
  }

  &::before {
    content: '';
    position: absolute;
    width: 2px;
    height: 10px;
    border-radius: 3px;
    background-color: #000000;
    // opacity: 0.7;
    top: 6px;
    left: 0px;
  }

  &--left::before {
    transform: rotate(-45deg);
  }

  &--left::after {
    transform: rotate(45deg);
  }

  &--right::before {
    transform: rotate(45deg);
  }

  &--right::after {
    transform: rotate(-45deg);
  }
}

//carousel
.carousel {
  margin: 0 auto;
  max-width: 730px;
  height: auto;

  .article-slider__img {
    width: 100%;
    height: 414px;
    object-fit: ccover;
  }

  // Кнопки навигации
  .slick-arrow {
    position: absolute;
    top: 30%;
    transform: translateY(-50%);
    border: none;
    background-color: none;
    padding: 0;
    cursor: pointer;
    font-size: 0;
    background-color: transparent;

    &::before {
      display: table;
      font-size: 40px;
      line-height: 1;
      background-color: transparent;
      color: black;
    }

    &:hover,
    &:focus {
      &::before {
        opacity: 0.7;
      }
    }
  }

  .slick-prev {
    left: 10px;
    z-index: 5;

    &::before {
      content: "❮";
    }
  }

  .slick-next {
    right: 20px;

    &::before {
      content: "❯";
    }
  }
}

/////////////////////////////// B l o g 1
.blog-one__item {
  p {
    margin-bottom: 40px;
  }

  blockquote {
    font-size: 24px;
    font-weight: 300;
    font-style: italic;
    color: $title;
    margin-bottom: 30px;
    padding: 0 40px;
  }

  .blog__blockquote-author::before {
    margin-right: auto;
    margin-left: 0;
  }

  .tags {
    margin-bottom: 40px;
  }
}

// blog-1 carousel
.blog-one__item-carousel {
  position: relative;
  margin: 0 auto;
  margin-bottom: 35px;
  max-width: 730px;
  height: 130px;

  p {
    margin: 39px 40px;
  }

  p.slick-current.slick-active {
    text-align: left;
  }

  p.slick-active {
    text-align: right;
  }

  &::after {
    content: '';
    position: absolute;
    width: 100%;
    height: 1px;
    background-color: #DFDFDF;
    bottom: 0;
    left: 0;
    right: 0;
  }

  &::before {
    content: '';
    position: absolute;
    width: 100%;
    height: 1px;
    background-color: #DFDFDF;
    top: 0;
    left: 0;
    right: 0;
  }

  // Кнопки навигации
  .slick-arrow {
    position: absolute;
    top: 50%;
    transform: translateY(-50%);
    border: none;
    background-color: none;
    padding: 0;
    cursor: pointer;
    font-size: 0;
    background-color: transparent;
    opacity: 0.7;

    &:hover {
      opacity: 1;
    }

    &::before {
      display: block;
      line-height: 1;
      background-color: transparent;
    }
  }

  .slick-prev {
    left: 0px;
    z-index: 5;

    &::before {
      content: "";
    }
  }

  .slick-next {
    right: 0px;

    &::before {
      content: "";
    }
  }
}

.blog-one {
  &__comments-title {
    color: $title;
    @extend %title-font;
    line-height: 31px;
    font-size: 20px;
    margin-bottom: 15px;
  }

  &__comments-item {
    display: flex;
    justify-content: space-between;
    align-items: flex-start;
    margin-bottom: 40px;
  }

  &__comments-avatar {
    width: 64px;
    height: 64px;
    object-fit: cover;
    border-radius: 50%;
    margin-right: 13px;
  }

  &__reply {
    background-color: transparent;
    border: none;
    padding: 0;
    font-size: 14px;
    margin-left: 5px;
    font-weight: 600;
    color: #707070;

    &:hover {
      color: black;
    }
  }

  &__comments-name {
    color: $title;
    @extend %title-font;
    line-height: 23px;
  }

  &__comments-date {
    margin-bottom: 10px;
    font-size: 14px;
    color: #a1a1a1;
  }




  //Форма на странице комментариев

  &__form {
    &-title {
      margin-bottom: 25px;
      color: $title;
      @extend %title-font;
      line-height: 23px;
    }
  }
}