.blog-box {
  margin-bottom: 100px;
  &__title {
    color: $title;
    @extend %title-font;
    font-size: 36px;
    line-height: 51px;
    margin: 0 auto 30px;
    text-align: center;
    max-width: 430px;
  }
  &__items {
    display: grid;
    grid-template-columns: 1fr 1fr;
    gap: 0 30px;
  }
  &__img-link {
    display: inline-block;
    margin-bottom: 19px;
  }

  &__links {
    margin-bottom: 9px;
    font-size: 14px;
    font-weight: 600;
    color: #707070;
  }

  &__date, &__author, &__theme {
    color: inherit;
    font-size: inherit;
  }

  &__item-title {
    color: $title;
    font-size: 24px;
    line-height: 38px;
    @extend %title-font;
  }
}