.decor {
  padding: 170px 0 155px;
  background-image: url('../img/decor.webp');
  background-size: cover;
  background-repeat: no-repeat;
  position: relative;
  &::before {
    content: '';
    position: absolute;
    background-color: rgba(255,255,255,.6);
    top: 0;
    bottom: 0;
    left: 0;
    right: 0;
  }
  &__title {
    @extend %title-font;
    max-width: 730px;
    color: $title;
    font-size: 60px;
    line-height: 80px;
    padding-bottom: 22px;
    margin-bottom: 22px;
    position: relative;
    &::after {
      content: '';
      position: absolute;
      left: 0;
      bottom: 0;
      width: 200px;
      background-color: #6d7278;
      height: 1px;
    }
  }
  &__text {
    max-width: 540px;
    color: black;
  }
}