.breadcrumbs {
  position: relative;
  z-index: 10;
}

.map {
  margin-top: -53px;
  margin-bottom: 50px;
}

.contact {
  margin-bottom: 100px;
  
  &__title {
    color: $title;
    font-size: 36px;
    @extend %title-font;
    line-height: 60px;
    padding-bottom: 30px;
    margin-bottom: 40px;
    position: relative;
    &::after {
      @extend %line;
      left: 0;
    }
  }
  &__inner {
    padding-left: 284px;
  }
  &__text {
    margin-bottom: 10px;
  }
  &__tel {
    color: $default;
  }
  &__city {
    margin-bottom: 16px;
  }  
}

.contact-slider {
  margin-bottom: 100px;
  width: 100%;
  &__link {
    position: relative;
    &:hover .contact-slider__title {
      opacity: 1;
    }
  }
  &__img {
    min-width: 170px;
    min-height: 170px;
    object-fit: cover;
  }
  &__title {
    transition: all 0.4s;
    position: absolute;
    left: 0;
    top: 0;
    right: 0;
    bottom: 0;
    display: flex;
    justify-content: center;
    align-items: center;
    background-color: rgba(#fff, 0.6);
    color: $title;
    @extend %title-font;
    line-height: 22px;
    padding: 0 10px;
    text-align: center;
    opacity: 0;
  }
}

