.inspiration {
  padding-top: 100px;
  position: relative;

  &::after {
    content: '';
    position: absolute;
    bottom: 0;
    left: 0;
    right: 0;
    background-image: url(../img/content/insp__bg.webp);
    background-position: top center;
    background-repeat: no-repeat;
    background-size: cover;
    height: 390px;
    z-index: -1;


    display: flex;
    justify-content: center;
    align-items: center;
  }

  &__inner {
    display: flex;
    justify-content: center;
    align-items: center;
  }

  &__images {
    margin-right: 78px;
    margin-left: -230px;
  }

  &__title {
    font-size: 36px;
    line-height: 60px;
    color: $title;
    @extend %title-font;
    padding-bottom: 16px;
    margin-bottom: 24px;
    position: relative;

    &::after {
      @extend %line;
      left: 0;
    }
  }

  &__text {
    margin-bottom: 30px;
  }

  &__content {
    max-width: 825px;
  }

  &__item {
    line-height: 23px;
    @extend %title-font;
    color: $title;
    margin-bottom: 33px;
  }

  &__item-content {
    display: flex;
    justify-content: space-between;
    padding-bottom: 14px;
    border-bottom: 2px solid #fff;
    border-radius: 4px;
  }

  &__line {
    height: 2px;
    border-radius: 4px;
    background-color: $title;
    margin-top: -2px;
  }
}

.partners {
  padding: 16px 0 60px;

  &__items {
    display: grid;
    grid-template-columns: repeat(5, 1fr);
  }

  &__item {
    margin: 0 auto;
  }
}

.collection--about {
  margin-bottom: 72px;
}