.gallery {
  padding: 80px 0 90px;
  &__buttons {
    text-align: center;
    margin-bottom: 40px;
  }
  &__btn {
    margin: 0 13px;
    background-color: transparent;
    border: 1px solid #454647;
    border-radius: 20px;
    padding: 6px 16px;
    color: #8f8d8d;
    font-size: 14px;
    font-weight: 600;
    cursor: pointer;
  }
  .mixitup-control-active {
    background-color: #FFEF35;
    color: $title;
    border: none;
  }
  &__items {
    display: grid;  
    grid-template-columns: repeat(4, 1fr);
    grid-template-rows: repeat(2, auto);
    grid-gap: 30px 35px;
  }
  &__item:nth-child(1) {
    grid-row: span 2;
    grid-column: span 2;
  }
  &__item img {
    object-fit: cover;
    height: 100%;
    width: 100%;
  }
  &.gallery-page {
    .gallery__item:nth-of-type(8) {
      grid-column: -1/-3;
      grid-row: 3/5;
    }
  }
}

.gallery__item {
  background-color: grey;
}

